import dbConnect, { collectionNameObj } from '@/lib/dbConnect';
import Image from 'next/image';
import Link from 'next/link';
import React from 'react';

const Services = async () => {
    const servicesCollection = dbConnect(collectionNameObj.servicesCollection);
    const data = await servicesCollection.find({}).toArray();
    
    return (
        <div className='grid grid-cols-12'>
            {data.map((item)=>{
                return (
                    <div className='col-span-12 md:col-span-6 lg:col-span-4' key={item._id}>
                        <Image src={item.img} width={314} height={208} alt={item.title}/>
                        <h2 className='text-xl font-semibold'>{item.title}</h2>
                        <p className='font-semibold text-orange-500'>Price: ${item.price}</p>
                        <Link href={`/services/${item._id}`}><FaArrowRightLong /></Link>
                    </div>
                )
            })}
        </div>
    );
};

export default Services;